import { compose, createStore, applyMiddleware } from 'redux';
import logger from 'redux-logger';
import { serverStoreBuilder, clientStoreBuilder } from './reducers';

var composedStore = compose(applyMiddleware(logger));

var buildClientStore = function buildClientStore(state) {
  return composedStore(createStore)(clientStoreBuilder(state));
};
var buildServerStore = function buildServerStore(state) {
  return createStore(serverStoreBuilder(state));
};

export { buildClientStore, buildServerStore };


